$global-variables: (customBlocksName: eightshift-block,maxCols: 12,baseFont: Inter,zIndex: (header: 100,drawer: 99,overlay: 98,),breakpoints: (mobile: 480,tablet: 960,desktop: 1920,large: 1921,),containers: (default: 66rem,),gutters: (none: 0,default: 1.25rem,big: 2.5rem,),sectionSpacing: (min: -300,max: 300,step: 10,),sectionInSpacing: (min: 0,max: 300,step: 10,),colors: (primary: #9973E3,black: #000000,light: #CCCCCC,white: #FFFFFF,ocean: #44AEDE,green: #29A3A3,red: #E84E4E,yellow: #F99922,blue: #4184C7,mist: #F0F8FF,dew: #FBF9FF,cream: #F4F3F0,eightshift: #0D3636,),);
// Playground styles.

$sidebar-width: 280px;
$light-gray-500: #E2E4E7;
$break-small: 600px;

.playground {
	@media (min-width: #{ ($break-small) }) {
		width: calc(100% - #{$sidebar-width});
	}
	padding-top: 20px;
	padding-bottom: 20px;
}

.playground__sidebar {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	width: $sidebar-width;
	border-left: 1px solid $light-gray-500;
	height: auto;
	overflow: auto;
	-webkit-overflow-scrolling: touch;

	// Temporarily disable the sidebar on mobile
	display: none;

	@media (min-width: #{ ($break-small) }) {
		display: block;
	}

	.block-editor-block-card {
		padding: 16px;
	}
}
